body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  height: 100%;
}

.bg-logo {
  background-image: url(/static/media/bg.7a622cb7.jpg);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#app {
  margin-top: 50px;
  padding-top: 50px;
  padding-bottom: 30px;
  border-radius: 10px;
}

#disclaimer {
  font-size: 9px;
}

@media (min-width: 375px) {
  #app {
    min-width: 350px;
    padding-top: 10px;
  }
}

a {
  text-decoration: none;
}
.App {
  text-align: center;
}

.links {
  text-align: right;
  margin-right: 10px;
  font-size: calc(6px + 2vmin);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 35vmin;
  pointer-events: none;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  font-size: calc(10px + 2vmin);
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  background-color: #282c34;
  color: #eee;
  font-family: 'Lato', Helvetica, sans-serif;
  font-size: 3vmin;
}

.hidden {
  display: none;
}

/** Buttons **/

.btn,
Button {
  background-color: transparent;
  border-radius: 2em;
  border: 2px solid #1DB954;
  color: white;
  cursor: pointer;
  font-size: 3vmin;
  padding: 0.7em 1.5em;
  transition: all 0.25s ease;
}

Button {
  font-size: 1.5vw;
  margin-left: 10px;
  padding: 7px 30px;
}

.btn:hover,
Button:hover {
  background: #1DB954;
  color: #333;
}

.btn--login {
  margin: 0 auto;
}

/** Background **/

.background {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  -webkit-filter: blur(8em) opacity(0.6);
          filter: blur(8em) opacity(0.6);
  position: absolute;
}

.main-wrapper {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  margin: 0 auto;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  width: 80%;
  z-index: 1;
}

.main-container {
  -webkit-flex: 1 1;
          flex: 1 1;
}


#buttonContainer {
  width: 50%
}

/* .userButton {
  font-size: 1.5vw;
  padding: 10px 50px;
} */

/* Typography */

a {
  text-decoration: none;
  color: white;


}

a:hover {
  text-decoration: none;
  color: #1DB954;
}

h1 {
  font-size: 4vw;
  font-family: 'Staatliches';

}

h2 {
  font-size: 3vw;
  font-family: 'Lato';
  font-weight: 700;
}

/* Playlist Styles */

form input {
  font-family: 'Lato';
  margin: 5px;
}

.Player button {
  border: 1px solid transparent;
  background-color: #1DB954;
  font-size: 1.5vw;
  margin: 70px 10px 10px;
}


#spacer {
  background-color: #1DB954;
}

#playlistTable {
  border: 1px solid #1DB954;
  margin-top: 20px;
}

#playlistTable>thead>tr>th {
  border: 1px solid #1DB954;
  background-color: rgba(29, 185, 84, 0.6)
}

#playlistTable>tbody>tr>td {
  border: 1px solid #1DB954;
  font-size: 1.5vw;
  font-weight: 400;
}

#currentSong {
  font-weight: 700;
}

.roomID {
  font-family: 'lato';
  font-weight: 700;
  font-size: 3vw;
  color: #1DB954;
  letter-spacing: 7px;
}
.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 35vmin;
    pointer-events: none;
    display: hide;
}

/** Now Playing **/

.now-playing__name {
    font-size: 1.5em;
    margin-bottom: 0.2em;
}

.now-playing__artist {
    margin-bottom: 1em;
}

.now-playing__status {
    margin-bottom: 1em;
}

.now-playing__img {
    float: left;
    margin-right: 10px;
    text-align: right;
    width: 45%;
}

.now-playing__img img {
    max-width: 80vmin;
    width: 100%;
}

.now-playing__side {
    margin-left: 5%;
    width: 45%;
}

/** Progress **/

.progress {
    border: 1px solid #eee;
    height: 6px;
    border-radius: 3px;
    overflow: hidden;
}

.progress__bar {
    background-color: #eee;
    height: 4px;
}
.Modal {
    position: absolute;
    top: 150px;
    left: 150px;
    right: 150px;
    bottom: 150px;
    background-color: #333;
    ;
}

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparent;
}

.content {
    margin: 125px 250px auto;

}

#close {
    -webkit-align-content: flex-start;
            align-content: flex-start;
    float: right;
    border: none;
    color: white;
}

#close:hover {
    background: none;
    color: #1DB954;
}

#submitNickname {
    border: none;
    border: 2px solid #1DB954;
    color: #1DB954;
    margin-left: 30px;
    padding: 12px 50px;
    margin: 10px;
}

#submitNickname:hover {
    background: #1DB954;
    color: #333;
}

#nickname {
    background: white;
    border-radius: 5px;
    padding: 10px;
}

.form-control-plaintext {
    padding: 5px;
    background-color: white;
    margin-bottom: 15px;
    margin-right: 100px;
}

#goButton {
    font-size: 1.5vw;
    padding: 10px 50px;
    margin: 10px;
    -webkit-align-content: center;
            align-content: center;
}

h4 {
    margin-bottom: 30px;
}

#formGroup {
    margin-right: 30px;
}
